@use "base/variables" as v;
@use "base/mixins" as m;

.footer {
  background-color: v.$secundario;
  padding-top: v.$separacion;

  &__grid {
    @include m.tablet {
      @include m.grid(3, 1.5rem);
    }
  }

  &__box {
    margin-bottom: v.$separacion;
    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__heading {
    color: v.$blanco;
    margin: 0;
    text-align: center;
  }

  &__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__nav,
  &__texto {
    margin-top: 2rem;
  }

  &__link,
  &__texto {
    font-size: 2rem;
    line-height: 1.8;
    color: v.$blanco;
    text-align: center;

    @include m.tablet {
      text-align: left;
    }
  }

  &__copyright {
    background-color: v.$primario;
    color: v.$blanco;
    text-align: center;
    padding: 2rem;
    margin: 0;
  }
}
